import React from 'react';
import { useParams } from 'react-router-dom';
import subview from '../img/subview.jpg';

function SubView({ imgs }) {
  const { sub_3 } = useParams();

  return (
    <>
      <div className="product_img">
        <img src={imgs[sub_3].largeImageURL} alt={imgs[sub_3].largeImageURL} />
      </div>
      <div className="product_info">
        <div className="title">  {parseInt(sub_3)+1}</div>
        <div className="price">139,000</div>
        <form action="">
          <select className="option_1 box_shadow">
            <option>  1 (+ 0 )</option>
            <option>  (+ 50,000)</option>
            <option>   (+ 10,000)</option>
          </select>
          <select className="option_2 box_shadow">
            <option>  2 (+ 0 )</option>
            <option>   (+ 5,000)</option>
          </select>
          <div className="quantity box_shadow">
            <label></label>
            <input type="number" min="1" max="10" step="1" defaultValue="1"></input>
          </div>
          <button className="buy box_shadow"><a href="/keyboard_surfer/"></a></button>
          <button className="wish_list box_shadow"><a href="/keyboard_surfer/"></a></button>
        </form>
      </div>
      <div className="sub_desc"><img src={subview} alt=" " /></div>
    </>
  );
}

export default SubView;