import React from 'react';
import { useParams } from 'react-router';

function BreadCrum() {
  const { sub_1, sub_2 } = useParams();
  
  const pageName = {
    sub_1: ' ',
    sub_2: ' ',
    sub_3: '66KEY ',
    sub_4: ' ',
    sub_5: '',
    notice: '',
    event: '',
  };
  if(sub_2) {
  return (
    <div id="breadcrum">{pageName[sub_2]}</div>
    );
  } else {
  return (
    <div id="breadcrum">{pageName[sub_1]}</div>
    );
  }
  
}

export default BreadCrum;